@import "../../App.scss";

$grid-inner-padding: 2rem;
$grid-inner-size: calc(
	(100vw - 2 * #{$h-padding-desktop}) / 3 - (3 * #{$grid-inner-padding})
);

.grid__container {
	display: grid;
    grid-template-columns: repeat(3, 2fr);
    grid-gap: $grid-inner-padding;
	padding: $v-padding-desktop $h-padding-desktop;
}

.sponsor__container {
	position: relative;
	width: $grid-inner-size;
	height: calc(#{$grid-inner-size} / 1.5);
	overflow: hidden;
    padding: $grid-inner-padding;
    @include transition;
}

.sponsor__container:hover{
    @include hoverEffect;
}

.sponsor__logo {
	align-items: center;
}

.sponsor__description {
	position: absolute;
	bottom: $grid-inner-padding;
}

.sponsor__description .sponsor__category {
	font-size: 1rem;
	font-weight: 800;
	margin-block-end: 0.5rem;
}

.sponsor__name {
	font-size: 1.5rem;
	font-weight: 400;
}
