$black: #000;
$white: #fff;

//color-varients
$theme-color: #FFC20E;
$red: #FF0E0E;
$amber: #FFC20E;
$green: #31FF0E;
$blue: #0EFFFA;
$violet: #5C0EFF;
$pink: #FF0E80;
$grey: grey;

$v-padding-desktop: 2.5rem;
$h-padding-desktop: 4rem;
$v-padding-mobile: 2rem;
$h-padding-mobile: 2rem;

$yOffset: -6vw;

$topOffset-desktop: 8.5rem;
$topOffset-tablet: 6rem;
$topOffset-mobile: 5rem;

.container {
  position: relative;
}

.container .contents {
  position: relative;
  top: $topOffset-desktop
}

@media screen and (max-width: 600px) {
  .container .contents {
    top: $topOffset-mobile
  }
}

@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {
  .container .contents {
    top: $topOffset-tablet
  }
}

@mixin hoverEffect {
    transform: scale(1.05);
    box-shadow: 20px 20px 50px rgba($color: $black, $alpha: 0.15);
}

@mixin transition {
    transition-duration: 0.3s;
}

//button

@mixin resetButton {
  outline: none;
  padding: 0.5rem;
  /* font settings */
  color: $black;
  font-family: 'Inter', 'source-han-sans-simplified-c', sans-serif; 
  font-weight: 900;
  font-size: 1.2rem;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: ease;
  transition-duration: 0.1s;
}

.btn {
  @include resetButton;
  background-color: $black;
  color: $white;
  border: solid 2px transparent;
}

.btn:hover {
  border: 2px solid $black;
  background-color: transparent;
  color: $black;
  transform: scale(1.1);
}

.btn:active {
  border: 2px solid $black;
  background-color: $black;
  color: $theme-color;
}

.btn-text {
    position: relative;
    top: -0.05em;
}

.text_btn__chn {
    position: relative;
    top: -0.2rem;
}

.text-btn {
  @include resetButton;
  border: 0;
  font-size: 1.2rem;
}

.text-btn:hover {
    background: rgba($color: $white, $alpha: 0.2);
    border: 0;
    transform: unset;
}

a {
  text-decoration: none;
  color: inherit;
}
